@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  color: #000000;
  background-color: #f2f2f2;
  box-sizing: border-box;
}
.overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 250;
}
.hidden {
  display: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .visually-hidden-desktop {
    display: none;
  }
}
/**
  * ===========================================================
  * PAGE HEADER
  * ===========================================================
*/
.page-header {
  width: 320px;
  margin: 0 auto;
  padding: 0;
}
.page-header__logo {
  height: 0;
  position: relative;
}
.page-header__top-logo {
  position: absolute;
  top: -57px;
  left: 50%;
  margin-left: -51px;
  width: 102px;
  height: 83px;
  z-index: 100;
}
/**
  * ===========================================================
  * PAGE HEADER tablet
  * ===========================================================
*/
@media (min-width: 768px) {
  .page-header {
    width: 768px;
    margin: 0 auto;
  }
  .page-header__top-logo {
    position: absolute;
    top: -57px;
    left: 50%;
    margin-left: -51px;
    width: 102px;
    height: 84px;
    z-index: 100;
  }
}
/**
  * ===========================================================
  * PAGE HEADER desktop
  * ===========================================================
*/
@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
    margin: 0 auto;
  }
  .page-header__top-logo {
    position: absolute;
    top: -57px;
    left: 50%;
    margin-left: -94px;
    width: 187px;
    height: 96px;
    z-index: 100;
  }
}
